@import url(https://fonts.googleapis.com/css2?family=Nunito:ital@1&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	box-sizing: border-box;
}

body {
	margin: 0;
  background: #fceabb;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #f8b500, #fceabb); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  font-family: 'Nunito', sans-serif;
}

.App {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	position: relative;
}

.logo {
	position: absolute;
	top: 0;
	left: 0;
	margin: -30px 0;
	width: 175px;
}

ul {
  list-style: none;
}

.suggestions {
  padding: 20px;
  border-radius: 5px;
background-color: rgba(255,255,255, 0.6);
text-align: center;
}

.suggestions li {
  cursor: pointer;
  margin: 5px 0;
  transition: transform 0.2s  ease;
}

.suggestions li:hover {
  transform: scale(1.1)
}

.city-btn {
	cursor: pointer;
}

.EventList {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
}

.event {
	background-color: whitesmoke;
	border-radius: 5px;
	padding: 30px;
	margin: 10px;
}


input {
	padding: 10px;
	margin: 5px;
	border-radius: 5px;
	border: none;
  font-size: 1rem;
}

input:focus {
	border: none;
	outline: none;
	box-shadow: none;
}

button {
	border: none;
	padding: 10px;
	border-radius: 5px;
	cursor: pointer;
  background-color: #fcae1e;
  font-size: 1rem;
}

button:hover {
  filter: brightness(110%);
}

@media screen and (max-width: 600px) {
	.EventList {
		grid-template-columns: 1fr;
	}
}

@media screen and (min-width: 600px) and (max-width: 1000px) {
	.EventList {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media screen and (min-width: 1000px) and (max-width: 1500px) {
	.EventList {
		grid-template-columns: repeat(3, 1fr);
	}
}

/* Make clicks pass-through */
#nprogress {
	pointer-events: none;
}

#nprogress .bar {
	background: #29d;

	position: fixed;
	z-index: 1031;
	top: 0;
	left: 0;

	width: 100%;
	height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
	display: block;
	position: absolute;
	right: 0px;
	width: 100px;
	height: 100%;
	box-shadow: 0 0 10px #29d, 0 0 5px #29d;
	opacity: 1;
	transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
	display: block;
	position: fixed;
	z-index: 1031;
	top: 15px;
	right: 15px;
}

#nprogress .spinner-icon {
	width: 18px;
	height: 18px;
	box-sizing: border-box;

	border: solid 2px transparent;
	border-top-color: #29d;
	border-left-color: #29d;
	border-radius: 50%;
	animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
	overflow: hidden;
	position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
	position: absolute;
}
@keyframes nprogress-spinner {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

